import { createSlice } from '@reduxjs/toolkit';
import { userApi } from './userApi';

const initialState = {
  name: '',
  email: '',
  token: '',
};

export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: { },
  extraReducers: (builder) => {
    //   - userSignupSuccess -     userApi endpoints signup   matchFulfilled     
    builder.addMatcher(userApi.endpoints.signup.matchFulfilled,
      (state, { payload }) => {
        const { user, token } = payload;
        
        state.email = user.email;
        state.name = user.name;
        state.token = token;
      }
    );

    //   - userLoginSuccess -     userApi endpoints login   matchFulfilled     
    builder.addMatcher(userApi.endpoints.login.matchFulfilled,
      (state, { payload }) => {
        const { user, token } = payload;
        state.email = user.email;
        state.name = user.name;
        state.token = token;
      }
    );

    //     - userLogout -     userApi endpoints logout   matchFulfilled        
    builder.addMatcher(userApi.endpoints.logout.matchFulfilled, (state) => {
      state.email = initialState.email;
      state.name = initialState.name;
      state.token = initialState.token;
    }); 

    //    -  userCurrentSuccess -     userApi endpoints currentUser   matchFulfilled      (    )
    builder.addMatcher(userApi.endpoints.currentUser.matchFulfilled,
      (state, actions) =>  {
        // const { user } = payload;
        return {...state, ...actions.payload}
      }
    );

    //    - userError -     userApi endpoints currentUser   matchRejected    token    (,  token )
    builder.addMatcher(userApi.endpoints.currentUser.matchRejected,
      (state, { payload }) => {
        if (payload?.status === 401) {
          state.token = '';
        }
      }
    );
  },
});

// Action creators are generated for each case reducer function
// export const { RegisterSuccess, getCurrentSuccess } = userSlice.actions;

export default userSlice.reducer;